.drupal-content {
  p {
    padding-bottom: var(--mu-spacing-sm);
    font-size: 1rem;
    line-height: 1.5rem;
  }
  strong {
    font-weight: 600;
  }
  em {
    font-style: italic;
  }
  ol, ul {
    li {
      position: relative;
      font-size: 1rem;
      line-height: 1.5rem;
      padding-left: 1.25em;
    }
    ol,
    ul {
      padding-left: 0.5em;
    }
  }
  ol {
    padding-bottom: var(--mu-spacing-sm);
    counter-reset: ol-counter;
    li {
      padding-bottom: var(--mu-spacing-xs);
      counter-increment: ol-counter;
      &:before {
        content: counter(ol-counter) ". ";
        position: absolute;
        left: 0;
      }
    }
  }
  ul {
    padding-bottom: var(--mu-spacing-sm);
    li {
      padding-bottom: var(--mu-spacing-xs);
      &:before {
        content: "";
        width: 0.875em;
        height: 0.875em;
        background-color: currentColor;
        -webkit-mask: url('../assets/dot-circle.svg') no-repeat center;
        mask: url('../assets/dot-circle.svg') no-repeat center;
        position: absolute;
        left: 0;
        top: 0.25em;
      }
      ul{
        li {
          padding-bottom: var(--mu-spacing-xs);
          &:before {
            content: "";
            height: 0.3em;
            background-color: currentColor;
            -webkit-mask: url('../assets/circle-solid.svg') no-repeat center;
            mask: url('../assets/circle-solid.svg') no-repeat center;
            position: absolute;
            left: 0;
            top: 0.5em;
          }
        }
      }
    }
  }

  img {
    max-width: 100%;
    margin: 0;
    height: auto;
  }
  details {
    margin: 0.5rem 0 1rem;
    summary {
      color: var(--ion-color-tertiary);
      list-style: none;
      cursor: pointer;
      position: relative;
      padding: 1rem 0;
      display: flex;
      flex-direction: row;
      order: 1;
      &::before {
        content: '';
        width: 1em;
        height: 1em;
        background-color: currentColor;
        -webkit-mask: url('../assets/chevron-down.svg') no-repeat center;
        mask: url('../assets/chevron-down.svg') no-repeat center;
        color: var(--ion-color-tertiary);
        order: 2;
        margin-left: 1em;
      }
      &::after {
        content:'';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 1px;
        background-color: var(--mu-color-border);
        order: 3;
      }
    }
    .details-wrapper {
      padding: 1rem 0;
    }
    &[open]{
      summary {
        &::before {
          transform: rotate(180deg);
        }
      }
    }
  }
  a[href] {
    color: var(--ion-color-tertiary);
    &:not([data-entity-substitution]):not([href^="mailto:"]):not([href^="tel:"]){
      &::after {
        content: "";
        width: 0.7em;
        height: 0.7em;
        background-color: currentColor;
        -webkit-mask: url('../assets/external-link-alt.svg') no-repeat center;
        mask: url('../assets/external-link-alt.svg') no-repeat center;
        color: var(--ion-color-tertiary);
        margin-left: 0.25em;
        display: inline-block;
      }
    }
    &[data-entity-substitution=media]{
      &::after {
        content: "";
        width: 0.7em;
        height: 0.7em;
        background-color: currentColor;
        -webkit-mask: url('../assets/download.svg') no-repeat center;
        mask: url('../assets/download.svg') no-repeat center;
        color: var(--ion-color-tertiary);
        margin-left: 0.25em;
        display: inline-block;
      }
    }
    &[href^="tel:"]{
      &::before {
        content: "";
        width: 0.7em;
        height: 0.7em;
        background-color: currentColor;
        -webkit-mask: url('../assets/phone-alt-solid.svg') no-repeat center;
        mask: url('../assets/phone-alt-solid.svg') no-repeat center;
        color: var(--ion-color-tertiary);
        margin-right: 0.25em;
        display: inline-block;
      }
    }
  }

  .field--name-field-media-oembed-video {
    .field__item {
      overflow: hidden;
      /* 16:9 aspect ratio */
      padding-top: 56.25%;
      position: relative;
      & > iframe {
        border: 0;
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
      }
    }
  }
  // make sure a svg image with the same name exists in ../assets
  @each $icon in facebook, twitter, linkedin, instagram, youtube, doc, docx, xls, xlsx, pdf, mail {
    .link-#{$icon}{
      &::before {
      content: "";
      width: 1em;
      height: 1em;
      margin-right: 0.25em;
      display: inline-block;
      vertical-align: middle;
      background-color: currentColor;
        -webkit-mask: url("../assets/#{$icon}.svg") no-repeat center;
        mask: url('../assets/#{$icon}.svg') no-repeat center;
        color: var(--color-#{$icon});
      };
      &::after {
        content: none !important;
      }
    }
  }
}
